import GlobalTypes from "./GlobalTypes";
import { postDataAPI,getTierAPI } from "../../utils/API";
import axios from 'axios';

export const WHITELIST_TYPES = {
	LOADING: "LOADING",
	GET_TIER: "GET_TIER"
};

export const addwhitelist1 = (white_list, id) => async (dispatch) => {
	try {
		dispatch({ type: GlobalTypes.NOTIFY, payload: { loading: true } });

		const res = await postDataAPI("createwhitelist1",{white_list1:white_list, id : id});

		dispatch({ type: GlobalTypes.NOTIFY, payload: { loading: false } });
		dispatch({
			type: GlobalTypes.NOTIFY,
			payload: {
				success: res.data.msg,
			},
		});

	} catch (err) {
		dispatch({
			type: GlobalTypes.NOTIFY,
			payload: {
				error: err.response.data.msg,
			},
		});
	}
};


