import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Pagination from "@material-ui/lab/Pagination";
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { getico, deleteico } from "../../redux/actions/icoAction";
import { NavLink } from "react-router-dom";
import { getstoreClaim, getsendClaimToken } from "../../redux/actions/claimTokenAction";
import { getDataAPI } from "../../utils/API";
import Modal from "@material-ui/core/Modal";

//componenet of upcoming pool.
const UpcommingPool = () => {
  const { ico } = useSelector((state) => state);
  const dispatch = useDispatch();
  const [page, setPage] = useState(1);

  useEffect(() => {
    dispatch(getico(page));
  }, [dispatch, page]);

  const handleChange = (event, value) => {
    setPage(value);
  };
  const useStyles = makeStyles(() => ({
    ul: {
      "& .MuiPaginationItem-root": {
        color: "#fff",
      },
    },
  }));
  const classes = useStyles();
  const clickClaimStore = (addr) => {
    dispatch(getstoreClaim(addr))
  }
  const clickSendToken = (addr) => {
    dispatch(getsendClaimToken(addr))
  }

  const downloadcsv = async (addr) => {
    await getDataAPI(`generate_csv/${addr}`)
    document.getElementById('download-link').click();
  }
  var upcomming = 0;
  if (ico.paginatedUpcomming_pool && ico.paginatedUpcomming_pool.length) {
    upcomming = 1;
  }
  const [open, setOpen] = useState(false);
  const [Id, setId] = useState('')
  const handleOpen = (id) => {
    setId(id)
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };


  const deletePool = () => {
    dispatch(deleteico(Id))
    setOpen(false)
    window.location.reload()
  }
  const body = (
    <div className="paper">
      <a className="cancel" href="#cancel" onClick={() => handleClose()} >
        Cancel
      </a>
      <a className="coming-soon" href="#confirm" onClick={deletePool} className="confirm">
        Confirm
      </a>
    </div>
  );
  return (
    <div>
      <div className="upcoming-list">
        {ico.paginatedUpcomming_pool
          ? ico.paginatedUpcomming_pool.map((upcpool) => (
            <div key={upcpool._id} className="inner-box">
              <div className="list-boxes">
                <div className="media">
                  <div className="client-name">
                    <div className="client-img">
                      <img
                        src={upcpool.images ? `/img/${upcpool.images}` : ""}
                        alt=""
                      />
                    </div>
                    <div className="client-info">
                      <h5>{upcpool.title}</h5>
                      <h6>{upcpool.address.slice(0, 5)}...{upcpool.address.slice(37, 42)}</h6>
                    </div>
                  </div>

                  <div className="update__delete">
                    <Link className="fa fa-trash" onClick={() => handleOpen(upcpool._id)}></Link>
                    <Link
                      to={`/admin/editico/${upcpool._id}`}
                      className="fa fa-pencil-square-o"
                    ></Link>
                  </div>
                </div>
                <div className="radio">Ratio per 1 BNB</div>
                <div className="na">
                  {upcpool.up_pool_raise}
                  <span> {upcpool.symbol}</span>
                </div>
                <div className="border"></div>
                <div className="percentage">
                  <span className="total">{upcpool.total_supply ? (upcpool.distribute_token / upcpool.total_supply).toFixed(4) * 100 : ""}%</span>
                  <span className="sfund">{upcpool.distribute_token}/{upcpool.total_supply} {upcpool.symbol}</span>
                </div>
              </div>
              <div className="text-center">
                {
                  upcpool.address ? <NavLink to={`/admin/adduserinwhitelist/${upcpool._id}`} className="detail">
                    Add User in White List
                  </NavLink> : ""
                }
                {/* {upcpool.address ? 
                <NavLink to={`/admin/updatetier/${upcpool.address}`} className="coming-soon">
                Update Tiers Value
                </NavLink>
                : ""} */}

                {/* {upcpool.address ? 
                 <Link to={`/admin/readwhitelist/${upcpool.address}`} className="coming-soon">
                 Read White List
                 </Link>
                 : ""} */}

                {/* {upcpool.address ? 
               <a href="#prepare-token" onClick={() => clickClaimStore(upcpool.address)} className="coming-soon">
               Prepare Claim
               </a>
               : ""} */}

                {/* {upcpool.address ? 
                  <a href="#distribute-token" onClick={() => clickSendToken(upcpool.address)} className="coming-soon">
                    Distribute Token
                  </a>
                  : ""} */}
                {/* {upcpool.address ? 
                  <a href="#download-csv" onClick={() => downloadcsv(upcpool.address)} className="coming-soon">
                    download csv
                  </a>
                  : ""} */}
                <a href="#comming-soon" className="coming-soon">
                  comming-soon
                </a>


              </div>

            </div>
          ))
          : ""}
      </div>
      <div>
        {upcomming ? "" : <h2
          style={{
            color: "white",
            width: "100%",
            height: "80vh",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
        >
          No records found
        </h2>}
      </div>
      <a href='/exportcsv/out.csv' download='out.csv' id='download-link'> </a>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <Pagination
          count={ico.totalUpcommingPage}
          page={page}
          defaultPage={page}
          color="primary"

          classes={{ ul: classes.ul }}
          onChange={handleChange}
        />
      </div>
      <div>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="simple-modal-title"
          aria-describedby="simple-modal-description">
          {body}
        </Modal>
      </div>
    </div>
  );
};

export default UpcommingPool;
