import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import "./Header.css";
import logo from "../../images/Logo.png";
import Modal from "@material-ui/core/Modal";
import Web3 from "web3";


import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import {disconnectWallet, connectWallet, checkWalletConnection} from  "../../redux/actions/metamaskAction";


const Header = () => {;
	const { auth, metamask } = useSelector((state) => state);
	const dispatch = useDispatch()
	const provider = Web3.givenProvider;

	//check connection
	useEffect(() => {
		dispatch(checkWalletConnection())
	},[dispatch, provider])
	
	//connect wallet
	const connectMetamask = async () => {
		dispatch(connectWallet())	
	}
	//disconnect wallet
	const disconnectwallet = () => {
		dispatch(disconnectWallet())
		handleClose()
	}
	const [open, setOpen] = React.useState(false);
	const handleOpen = () => {
		setOpen(true);
	};
	const handleClose = () => {
		setOpen(false);
	};

	const body = (
		<div className="paper">
			<h2 className="paper_h2" id="simple-modal-title">Your wallet</h2>
			<br />
			<input
				className="paper_input"
				type="text"
				name="amount"
				value={metamask.address}
				readOnly
			/>{" "}
			<br />
			<br />
			<button
				className="paper_button"
				onClick={() => disconnectwallet()}>
				Disconnect
			</button>
		</div>
	);


	return (
		<div>
			<header className="hea_der">
				<div className="container_cust">
					<div className="inner_header">
						<div className="logo">
							<Link to="/">
								<img src={logo} alt="" />
							</Link>
						</div>
						<div className="navi_gation">

							<Link
								className="gen_btn btn_white"
								to={
									auth.token && auth.role
										? "/admin/upcommingpool"
										: "/admin/login"
								}>
								dashboard
							</Link>
							 <div className="gen_bttn">
								{metamask.active ? (
									<button
										className="gen_bttn btn_white connect_btn yellow_btn"
										onClick={() => connectMetamask()}>
										connect wallet
									</button>
								) : ""}

								<div>
									{metamask.design ? (
										<div>
											<button
												className="addressh"
												onClick={() => handleOpen()}>
												{metamask.showAddress}
											</button>
										</div>
									) : null}
								</div>
							</div> 
						</div>
					</div>
				</div>
			</header>
			<div>
				<Modal
					open={open}
					onClose={handleClose}
					aria-labelledby="simple-modal-title"
					aria-describedby="simple-modal-description">
					{body}
				</Modal>
			</div>
		</div>
	);
};

export default Header;
