
import { Link, NavLink } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Pagination from "@material-ui/lab/Pagination";
import { makeStyles } from "@material-ui/core/styles";
import { useDispatch, useSelector } from "react-redux";
import { getico, deleteico } from "../../redux/actions/icoAction";

import {
	getstoreClaim,
	getsendClaimToken,
} from "../../redux/actions/claimTokenAction";
import Modal from "@material-ui/core/Modal";
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'


toast.configure();
//featured pool component.
const FeaturedPool = () => {
	const { ico } = useSelector((state) => state);
	const dispatch = useDispatch();
	const [page, setPage] = useState(1);
	const [open, setOpen] = useState(false);
	const [Id, setId] = useState('');
	const [checkcsv, setCheckcsv] = useState(false)

    const handleOpen = (id) => {
		setId(id)
		setOpen(true);
	};
	const handleClose = () => {
		setOpen(false);
	};
	useEffect(() => {
		dispatch(getico(page));

	}, [dispatch, page]);
	const handleChange = (event, value) => {
		setPage(value);
	};
	const useStyles = makeStyles(() => ({
		ul: {
			"& .MuiPaginationItem-root": {
				color: "#fff",
			},
		},
	}));
	const classes = useStyles();
	const clickClaimStore = (addr) => {
		dispatch(getstoreClaim(addr));
		setCheckcsv(true)
	};
	const clickSendToken = (addr) => {
		dispatch(getsendClaimToken(addr));
	};
	const downloadcsv = (name) => {
		var link = document.createElement('a');
		link.href = `/exportcsv/${name}-allocation.csv`;
		document.body.appendChild(link);
		link.click();
		document.body.removeChild(link);
		setCheckcsv(false)
    }
	var featured = 0;
	if (ico.paginatedFeatured_pool && ico.paginatedFeatured_pool.length) {
		featured = 1;
	}
    const deletePool = () => {
		dispatch(deleteico(Id))
		setOpen(false)
		window.location.reload()
	}
    const body = (
		<div className="paper">
			<a className="cancel" href="#cancel" onClick={() => handleClose()} >
				Cancel
			</a>
			<a className="coming-soon" href="#confirm" onClick={deletePool} className="confirm">
				Confirm
			</a>
		</div>
	);
	return (
		<div>
			<div className="upcoming-list">
				{ico.paginatedFeatured_pool
					? ico.paginatedFeatured_pool.map((featured) => (
						<div key={featured._id} className="inner-box">
							<div className="list-boxes">
								<div className="media">
									<div className="client-name">
										<div className="client-img">
											<img
												src={featured.images ? `/img/${featured.images}` : ""}
												alt=""
											/>
										</div>
										<div className="client-info">
											<h5>{featured.title}</h5>
											<h6>
												{featured.address.slice(0, 5)}...
												{featured.address.slice(37, 42)}
											</h6>
										</div>
									</div>

									<div className="update__delete">
										<Link className="fa fa-trash" onClick={() => handleOpen(featured._id)}></Link>
										<Link
											to={`/admin/editico/${featured._id}`}
											className="fa fa-pencil-square-o"></Link>
									</div>
								</div>
								<div className="radio">Ratio per 1 BNB</div>
								<div className="na">
									{featured.up_pool_raise}
									<span> {featured.symbol}</span>
								</div>
								<div className="border"></div>
								<div className="percentage">
									<span className="total">{featured.total_supply ? (featured.distribute_token / featured.total_supply).toFixed(4) * 100 : ""}%</span>
									<span className="sfund">
										{featured.distribute_token}/{featured.total_supply} {featured.symbol}
									</span>
								</div>
							</div>

							<div className="text-center">
								{/* <a href={`/pool_detail/featured/${featured._id}`} target="_blank" className="detail">
                    details
                  </a>
                  <a href="#ff" className="coming-soon">
                    FEATURED
                  </a> */}
								{/* <NavLink
										to={`/admin/transferownership/${featured._id}`}
										className="detail">
										Transfer Ownership
									</NavLink> */}
								{featured.address ? <NavLink
									to={`/admin/adduserinwhitelist/${featured._id}`}
									className="detail">
									Add User in White List
								</NavLink> : ""}

								{/* {featured.address ?
									<NavLink
										to={`/admin/updatetier/${featured.address}`}
										className="coming-soon">
										Update Tiers Value
									</NavLink>
									: ""} */}
								{/* {featured.address ?
									<Link
										to={`/admin/readwhitelist/${featured.address}`}
										className="coming-soon">
										Read White List
									</Link>
									: ""} */}
								{featured.address ?
									<a
										href="#prepare-token"
										onClick={() => clickClaimStore(featured.address)}
										className="coming-soon">
										Prepare Claim
									</a> : ""}
								{featured.address ?
									<Link
										to="#distribute-token"
										onClick={() => clickSendToken(featured.address)}
										className="coming-soon">
										Distribute Token
									</Link>
									: ""}

								{featured.address ?
									<Link
										href="#download-csv"
										onClick={() => downloadcsv(featured.title)}
										className="coming-soon">
										download csv
									</Link> : ""}
							</div>
						</div>)) : ""}
			        </div>
			     <div>
				{featured ? "" : <h2
					style={{
						color: "white",
						width: "100%",
						height: "80vh",
						display: "flex",
						alignItems: "center",
						justifyContent: "center",
					}}
				>
					No records found
				</h2>}
			</div>

			<div style={{ display: "flex", justifyContent: "center" }}>
				<Pagination
					count={ico.totalfeaturedPage}
					page={page}
					defaultPage={page}
					color="primary"
					classes={{ ul: classes.ul }}
					onChange={handleChange}
				/>
			</div>
			<div>
				<Modal
					open={open}
					onClose={handleClose}
					aria-labelledby="simple-modal-title"
					aria-describedby="simple-modal-description">
					{body}
				</Modal>
			</div>
		</div>
	);
};

export default FeaturedPool;