import "./FeaturedPool.css";
import React, { useState, useEffect } from "react";
import Web3 from "web3";
import Modal from "@material-ui/core/Modal";
import { Link } from 'react-router-dom'
import { SeedifyFundsContractAbi } from "../abis";

//web3 provider.
const web3 = new Web3(
	new Web3.providers.HttpProvider(
		"https://bsc-dataseed.binance.org/",
	),
);

//featured pool
const FeaturedPool = ({ pool }) => {
	var x = pool.images;
	const [number2, setNumber2] = useState();
	const [number1, setNumber1] = useState();

	var contractAddr = '';
	if (pool.address) {
		contractAddr = pool.address.toUpperCase();
	}

	useEffect(() => {
		async function simpleContract() {
			if (contractAddr) {
				const SimpleContract = new web3.eth.Contract(
					SeedifyFundsContractAbi,
					contractAddr,
				);
				//to get total bnb in all tiers from blockchain.
				const result = await SimpleContract.methods.totalBnbReceivedInAllTier().call();
				//to get total maxcap from blockchain.
				const total = await SimpleContract.methods.maxCap().call();
				setNumber2(result / 10 ** 18);
				setNumber1(((result / 10 ** 18) / (total / 10 ** 18)) * 100);
			}
		}
		simpleContract()
		return () => { }
	}, [contractAddr]);

	var allocation = number2 * pool.up_pool_raise;
	useEffect(() => {
		setInterval(async function () {
			if (contractAddr) {
				const SimpleContract = new web3.eth.Contract(
					SeedifyFundsContractAbi,
					contractAddr,
				);
				//to get total bnb in all tiers from blockchain.
				const result = await SimpleContract.methods.totalBnbReceivedInAllTier().call();
				//to get total maxcap from blockchain.
				const total = await SimpleContract.methods.maxCap().call();
				setNumber2(result / 10 ** 18);
				setNumber1(((result / 10 ** 18) / (total / 10 ** 18)) * 100);
			}
		}, 20000)
		return () => { }
	}, [contractAddr]);

	const [open, setOpen] = useState(false);
	const handleOpen = () => {
		setOpen(true);
	};
	const handleClose = () => {
		setOpen(false);
	};

	if (pool.content) {
		var length = pool.content.length
		if (length > 210) {
			length = 1;
		} else {
			length = 0;
		}
	}
	const body = (
		<div className="paper">
			<h2 className="paper_h2" id="simple-modal-title">Read More</h2>
			<br />
			<p>{pool.content}</p>
			<br />
			<br />

		</div>
	);

	return (
		<div className="pool_card">
			<Link
				key={pool._id}
				to={`/pool_detail/featured/${pool._id}`}>
				<div className="tit_le">
					<img src={x ? `/img/${x}` : ""} alt="" />
					<h3>
						<div style={{ height: 55 }}>
							{pool.title}
						</div>
						<span>
							1 BNB = {pool.up_pool_raise} {pool.symbol}
						</span>
					</h3>
				</div>
			</Link>
			<div style={{ height: 70 }}>
				<p>
					{length ? pool.content.substring(0, 200) : pool.content}{length ? "..." : ""}{length ? <a href="#read-more" onClick={handleOpen}>Read More</a> : ""}
				</p>
			</div>
			<div className="raise">
				<p className="total_raise">Total Raise</p>
				<h2>{number2 ? number2.toFixed(4) : "0"} BNB</h2>
			</div>
			<div className="prog_bar">
				<div className="prog_bar_grd">
					<span className="prog">Progress</span>
					<span className="parti">
						Max Participants <span className="white_text">{pool.participants}</span>
					</span>
				</div>
				<div className="progress">
					<div className="bar" style={{ width: `${number1}%` }}>
						<p className="percent">{number1}</p>
					</div>
				</div>
				<div className="prog_bar_grd">
					{<span className="prog _percent">{number1 ? number1.toFixed(2) : "0"}%</span>}
					{
						<span className="parti _nls">
							{allocation ? allocation.toFixed(2) : "0"}/{pool.total_supply} {pool.symbol}
						</span>
					}
				</div>
			</div>
			<div className="allocation">
				<div>
					<p className="feature_max">Maximum</p>
					<h3>{pool.max_allocation ? pool.max_allocation + " BNB" : "TBA"}</h3>
				</div>
				<div>
					<p className="feature_max">Access</p>
					<h3>{pool.up_pool_access}</h3>
				</div>
			</div>
			<div>
				<Modal
					open={open}
					onClose={handleClose}
					aria-labelledby="simple-modal-title"
					aria-describedby="simple-modal-description">
					{body}
				</Modal>
			</div>
		</div>
	);
};

export default FeaturedPool;
