import GlobalTypes from "./GlobalTypes";
import Web3 from "web3";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

toast.configure()
export const METAMASK_TYPES = {
    LOADING: "LOADING",
    CONNECT: "CONNECT",

};

export const checkWalletConnection = () => async (dispatch) => {
    try {
        const address = localStorage.getItem("address");

        if (address && address !== 'undefined') {

            const providerOptions = {
                walletconnect: {
                    package: WalletConnectProvider,
                    options: {
                        rpc: {
                            56: 'https://bsc-dataseed.binance.org/'
                        },
                        network: 'binance',
                    }
                }
            };
            const web3Modal = new Web3Modal({
                cacheProvider: true,
                providerOptions
            });

            const provider = Web3.givenProvider;
            const chainid = provider.chainId;
            if (chainid === "0x38" || chainid === 56) {
                dispatch({
                    type: METAMASK_TYPES.CONNECT,
                    payload: {
                        address: address,
                        active: false,
                        design: true,
                        showAddress: address.slice(0, 4) + "..." + address.slice(38, 42),
                    },
                });
            }
            if (provider.selectedAddress !== "") {
                console.log('provider selectAddressss')

                //address = (provider.chainId === "0x38") ? provider.selectedAddress : (provider?.accounts[0]);

                console.log('provider selectAddressss')
                localStorage.setItem('address', address)

                provider.on("connect", (chainid) => {
                    if (chainid !== "0x38" || chainid !== 56) {
                        dispatch({
                            type: METAMASK_TYPES.CONNECT,
                            payload: {
                                active: true,
                                design: false,
                                showAddress: '',
                            },
                        });
                    }
                });
                provider.on("disconnect", (code: number, reason: string) => {
                    dispatch({
                        type: METAMASK_TYPES.CONNECT,
                        payload: {
                            active: true,
                            design: false,
                            showAddress: '',
                        },
                    });
                });
                provider.on("accountsChanged", (accounts: string[]) => {
                    if (provider.chainId !== "0x38" || provider.chainId !== 56) {
                        dispatch({
                            type: METAMASK_TYPES.CONNECT,
                            payload: {
                                active: true,
                                design: false,
                                showAddress: '',
                            },
                        });
                    }
                    else {
                        console.log('provider selectAddressss')
                        address = accounts[0];
                        if (accounts.length > 0) {
                            dispatch({
                                type: METAMASK_TYPES.CONNECT,
                                payload: {
                                    address: address,
                                    active: false,
                                    design: true,
                                    showAddress: address.slice(0, 4) + "..." + address.slice(38, 42),
                                },
                            });
                            localStorage.setItem('address', address)
                        } else {
                            localStorage.removeItem("address");
                            dispatch({
                                type: METAMASK_TYPES.CONNECT,
                                payload: {
                                    address: '',
                                    active: true,
                                    design: false,
                                    showAddress: '',
                                },
                            });

                        }

                    }
                });

                provider.on("chainChanged", (chainId: number) => {

                    if (chainId !== "0x38" || chainId !== 56) {
                        web3Modal.clearCachedProvider();
                        dispatch({
                            type: METAMASK_TYPES.CONNECT,
                            payload: {
                                active: true,
                                design: false,
                                showAddress: '',
                            },
                        });
                        toast.info("Please switch to Binance Smart Chain!", { position: toast.POSITION.TOP_CENTER })
                    }
                    if (chainId === "0x38" || chainId.chainId === 56) {
                        dispatch({
                            type: METAMASK_TYPES.CONNECT,
                            payload: {
                                address: address,
                                active: false,
                                design: true,
                                showAddress: address.slice(0, 4) + "..." + address.slice(38, 42),
                            },
                        });
                    }
                });
            }
        } else {
            dispatch({
                type: METAMASK_TYPES.CONNECT,
                payload: {
                    active: true,
                    design: false,
                    showAddress: '',
                },
            });
        }

    } catch (err) {
        dispatch({
            type: GlobalTypes.NOTIFY,
            payload: {
                error: err.response.data.msg,
            },
        });
    }
};

export const connectWallet = () => async (dispatch) => {
    try {
        const providerOptions = {
            walletconnect: {
                package: WalletConnectProvider,
                options: {
                    rpc: {
                        56: 'https://bsc-dataseed.binance.org/'
                    },
                    network: 'binance',
                }
            }
        };
        const web3Modal = new Web3Modal({
            cacheProvider: true,
            providerOptions
        });
        //connect

        const provider = await web3Modal.connect();
        window.provider = provider;
        const web3 = new Web3(provider);
        //get address
        const accounts = await web3.eth.getAccounts();
        const address = accounts[0];
        window.addressselected = accounts[0];
        window.checkconnect = address;
        // //get network id

        const chainid = await web3.eth.net.getId();
        if (chainid !== 56) {
            return toast.info("Connect your wallet to Binance Smart Chain!", { position: toast.POSITION.TOP_CENTER })
        }

        if (chainid === 56) {
            localStorage.setItem('address', address)
            provider.on("disconnect", (code: number, reason: string) => {
                dispatch({
                    type: METAMASK_TYPES.CONNECT,
                    payload: {
                        provider: false,
                        active: true,
                        design: false,
                    },
                });

            });
            provider.on("accountsChanged", (accounts: string[]) => {
                const address = accounts[0];
                if (accounts.length > 0) {
                    dispatch({
                        type: METAMASK_TYPES.CONNECT,
                        payload: {
                            address: address,
                            provider: provider,
                            active: false,
                            design: true,
                            showAddress: address.slice(0, 4) + "..." + address.slice(38, 42),
                        },
                    });
                    
                    localStorage.setItem('address', address)
                } else {
                    dispatch({
                        type: METAMASK_TYPES.CONNECT,
                        payload: {
                            address: '',
                            active: true,
                            design: false,
                            showAddress: '',
                        },
                    });
                    localStorage.removeItem('address',address)                
                }
            });
           
            provider.on("chainChanged", (chainId: number) => {
                if (chainId !== 56) {
                    web3Modal.clearCachedProvider();
                    dispatch({
                        type: METAMASK_TYPES.CONNECT,
                        payload: {
                            address: '',
                            active: true,
                            design: false,
                            showAddress: '',
                        },
                    });
                    toast.info("Please switch to Binance Smart Chain", { position: toast.POSITION.TOP_CENTER })
                }
                if (chainId === 56) {
                    dispatch({
                        type: METAMASK_TYPES.CONNECT,
                        payload: {
                            address: address,
                            provider: provider,
                            active: false,
                            design: true,
                            showAddress: address.slice(0, 4) + "..." + address.slice(38, 42),
                        },
                    });
                }
            });
            if (chainid === 56) {
                dispatch({
                    type: METAMASK_TYPES.CONNECT,
                    payload: {
                        address: address,
                        provider: provider,
                        active: false,
                        design: true,
                        showAddress: address.slice(0, 4) + "..." + address.slice(38, 42),
                    },
                });

            } else {
                localStorage.removeItem("WEB3_CONNECT_CACHED_PROVIDER");
                toast.info("Please switch to Binance Smart Chain", { position: toast.POSITION.TOP_CENTER })
            }
        }

    } catch (err) {
        dispatch({
            type: GlobalTypes.NOTIFY,
            payload: {
                error: err.response.data.msg,
            },
        });
    }
};

export const disconnectWallet = () => async (dispatch) => {
    try {
        dispatch({ type: GlobalTypes.NOTIFY, payload: { loading: true } });
        localStorage.removeItem("WEB3_CONNECT_CACHED_PROVIDER");
        localStorage.removeItem("address");
        dispatch({
            type: METAMASK_TYPES.CONNECT,
            payload: {
                active: true,
                design: false,
            },
        });
        dispatch({ type: GlobalTypes.NOTIFY, payload: { loading: false } });
    } catch (err) {
        dispatch({
            type: GlobalTypes.NOTIFY,
            payload: {
                error: err.response.data.msg,
            },
        });
    }
};