import React, { useState } from "react";
import "./Pool.css";
import Modal from "@material-ui/core/Modal";
import { Link } from 'react-router-dom'

//pool component to set the information inside particular pool.
const Pool = ({ pool }) => {
	var x = pool.images;
	if (pool.time_duration < 0) {
		var startcheck = 0;
	} else {
		startcheck = 1;
	}
	const [open, setOpen] = useState(false);
	const handleOpen = () => {
		setOpen(true);
	};
	const handleClose = () => {
		setOpen(false);
	};

	if (pool.content) {
		var length = pool.content.length
		if (length > 210) {
			length = 1;
		} else {
			length = 0;
		}
	}
	const body = (
		<div className="paper">
			<h2 className="paper_h2" id="simple-modal-title">Read More</h2>
			<br />
			<p>{pool.content}</p>
			<br />
			<br />

		</div>
	);

	return (
		<div className="pool_card">
			<Link key={pool._id} to={`/pool_detail/upcomming/${pool._id}`}>
				<div className="tit_le">
					<img className="image_circle" src={x ? `/img/${x}` : ""} alt="" />
					<h3>
						{pool.title}
						{startcheck ? <span>In {pool.time_duration} days</span> : <span>TBA</span>}
					</h3>
				</div>
			</Link>
			<p>
				{length ? pool.content.substring(0, 180) : pool.content}{length ? "..." : ""}{length ? <a href="#read-more" onClick={handleOpen}>Read More</a> : ""}
			</p>
			<div className="raise">
				<p className="total_raise"></p>
			</div>

			<div className="allocation">
				<div>
					<p>Min Allocation</p>
					<h3>{pool.min_allocation ? pool.min_allocation : "TBA"}</h3>
				</div>
				<div>
					<p>Max Allocation</p>
					<h3>{pool.max_allocation ? pool.max_allocation : "TBA"}</h3>
				</div>
				<div>
					<p>Access</p>
					<h3>{pool.up_pool_access}</h3>
				</div>
			</div>
			<div>
				<Modal
					open={open}
					onClose={handleClose}
					aria-labelledby="simple-modal-title"
					aria-describedby="simple-modal-description">
					{body}
				</Modal>
			</div>
		</div>
	);
};

export default Pool;
